*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
}
.App {
  text-align: center;
  position: relative    ;
}


.coat{
    background-color: red;
    position: absolute;
    left: 100px;
    top: 22px;
    cursor: pointer;
    font-size: 13px;
}
.coat_man, .coat_woman{
    color: black;
    background-color: rgb(224, 224, 224);
    padding: 10px;
}
.coat_man:hover, .coat_woman:hover{
    background-color: rgb(150, 150, 150)
}




.ganada{
    position: absolute;
    left: 210px;
    top: 22px;
    cursor: pointer;
    font-size: 13px;
}
.ganada_button{
    background-color: rgb(224, 224, 224);
    padding: 10px;
    color: black;
}
.ganada_button:hover{
    background-color: rgb(150, 150, 150)
}




.modal{
    margin-top: 20px;
    padding: 20px;
    background-color: #eee;
    text-align: left;
}






div{
    box-sizing: border-box;
}
.list{
    display: block;
    padding-left: 20px;/*list클래스 안의 요소에 왼쪽 padding을 줌*/
    text-align: left;/*왼쪽으로 글자들을 나열하게 설정함*/
    border-bottom: 1px solid grey;/*1px두께의 회색 선을 생성함*/
    height: 100px;
    padding: 20px;
}
.black-nav{
    display: inline-block;
    height: 60px;
    padding: 20px;
    display: flex;/**/
    background-color: black;/*div영역의 배경을 검정으로 설정함*/
    width: 100%;/*해당 div의 영역을 가로로 꽉차게 설정함*/
    color: white;/*글자의 색깔을 하얀색으로 설정하였다.*/
    padding-left: 20px;/*왼쪽에 padding을 넣어서 글자의 간격을 넓힘*/
}






.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
